import React from 'react'
import {useSelector} from 'react-redux'
import {useHistory, useParams} from 'react-router-dom'
import {
    Col,Card, CardBody
} from 'reactstrap'
import { JobDetails} from '../../components-job-detail'




export const CreateJobDetail = ({ reDirect=''}) => {
    const {personalDetailId, companyId} = useParams();
    const history = useHistory();

    const jobDetailReducer = useSelector(state => state.jobDetailReducer);
    const {data: {Id}} = jobDetailReducer;
    
    return (
        <>
            <Col lg="6">
                <Card className="border-0">
                    <CardBody className="card-body">
                        <h5 className="font-weight-bold mb-3">{!Id ? 'Crea Informacin Laboral del Empleado' : 'Actualiza Informacin Laboral del Empleado'}</h5>
                        <JobDetails companyId={companyId} personalDetailId={personalDetailId} history={history} reDirect={reDirect} />
                    </CardBody>
                </Card>
            </Col>
        </>
    )
}

export const UpdateJobDetail = ({reDirect=''}) => {
    const {personalDetailId, companyId} = useParams();
    const history = useHistory();
    return (
        <>            
            <Col lg="6">
                <Card className="border-0">
                    <CardBody className="card-body">
                        <h5 className="font-weight-bold mb-3">Actualiza Informacin Laboral del Empleado</h5>
                        <JobDetails companyId={companyId} personalDetailId={personalDetailId}  history={history} reDirect={reDirect} />
                    </CardBody>
                </Card>
            </Col>
        </>
    )
}